*{box-sizing:border-box;padding:0px;margin:0px;}
html,body,#root{width:100vw;height:100vh;position:relative;display:grid;justify-items:center;align-items:center;
background-color:var(--background-color);



}
:root{
--bodyBackgroundColor:#e76f6f;
--bodyBackgroundGradient:linear-gradient(18deg,#e76f6f 30%,#ffe5e5 80%);



/* from backup */
--background-color:rgb(167, 167, 167);
--borderColor:rgb(82, 81, 81);
--box-shadow-color:rgb(77, 77, 77);
--box-shadow-y:7px;
--box-shadow-x:3px;
}
::-webkit-scrollbar{
width:10px;
z-index:-100;
/* box-shadow:1px 1px black; */
}
::-webkit-scrollbar-track{
background:white;
/* box-shadow:black 2px 2px; */
/* border:solid black 1px; */
}
::-webkit-scrollbar-thumb{
background:var(--background-color);
}
input:hover, ::-webkit-scrollbar-thumb:hover{
opacity:0.7;
box-shadow:none;
}
::-webkit-scrollbar-thumb:hover{
opacity:0.7;
box-shadow:none;
border:solid black 1px;
}
.hidden{display:none;}
input[type="range"]::-webkit-slider-thumb{
-webkit-appearance: none;
background-color:var(--borderColor);
border: solid black 1px;
width:15px;
height:15px;
border-radius:50%;
/* opacity:0.5; */
/* box-shadow:var(--borderColor) 1px 2px; */
}
